import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";

/* FontAwesome */
import { library } from '@fortawesome/fontawesome-svg-core';
import { faAngleDown, faComment, faTags, faCircleDot, faMagnifyingGlass, faArrowLeft, faLink } from '@fortawesome/free-solid-svg-icons';
library.add(faAngleDown, faComment, faTags, faCircleDot, faMagnifyingGlass, faArrowLeft, faLink);
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';

const app = createApp(App);
app.use(store);
app.use(router);
app.component('font-awesome-icon', FontAwesomeIcon);
app.mount("#app");
