<template>
  <div class="iBanner">
    <swiper
      :modules="modules"
      :slides-per-view="1"
      :space-between="50"
      :pagination="{ clickable: true }"
      :autoplay="{ delay: 5000 }"
      :navigation="true"
      :effect="'fade'"
      @swiper="onSwiper"
      @slideChange="onSlideChange"
    >
      <swiper-slide v-for="pic in carousel" :key="pic">
        <span class="foreground">
          <span class="box">
            <span class="subject">{{ pic.title }}</span>
            <span class="summary">{{ pic.summary }}</span>
            <div class="btn-wrap">
              <router-link :to="pic.link" v-if="pic.title === 'Web'">
                <div class="g-btn"><span>觀看更多</span></div>
              </router-link>
              <a :href="pic.link" target="_blank" v-else>
                <div class="g-btn"><span>觀看更多</span></div>
              </a>
            </div>
          </span>
        </span>
        <div class="iBanPic" :style="'background-image: url(' + pic.img + ')'"></div>
      </swiper-slide>
      <!-- <swiper-slide><img src="@/assets/images/i_banner_1.jpg" alt="thermaltake"></swiper-slide> -->
      
    </swiper>
    <div class="scroll"></div>
  </div>
</template>

<style>
.iBanner {
  position: relative;
}
.scroll {
  position: absolute;
  bottom: 20px;
  left: 50%;
  z-index: 1;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
.scroll::before, .scroll::after {
  pointer-events: none;
  content: "";
  position: absolute;
  left: 50%;
  bottom: -20px;
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
}
.scroll:before {
  background-color: #707070;
  width: 1px;
  height: 40px;
  display: block;
}
.scroll::after {
  margin-left: -2px;
  -webkit-transform-origin: center top;
  transform-origin: center top;
  position: absolute;
  width: 10px;
  height: 10px;
  border-right: 1px solid #707070;
  border-top: 1px solid #707070;
  -webkit-animation: upDown 2.5s ease both infinite;
  animation: upDown 2.5s ease both infinite;
}
@keyframes upDown {
	0% {
		-webkit-transform: translateY(-25px);
		transform: translateY(-25px) rotate(135deg);
	}
	50% {
		-webkit-transform: translateY(7px);
		transform: translateY(7px) rotate(135deg);
	}
	100% {
		-webkit-transform: translateY(-25px);
		transform: translateY(-25px) rotate(135deg);
	}
}
@-webkit-keyframes upDown {
	0% {
		-webkit-transform: translateY(-25px);
		transform: translateY(-25px) rotate(135deg);
	}
	50% {
    -webkit-transform: translateY(7px);
    transform: translateY(7px) rotate(135deg);
	}
	100% {
		-webkit-transform: translateY(-25px);
		transform: translateY(-25px) rotate(135deg);
	}
}
.iBanner .swiper img {
  display: block;
}
.iBanner .swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .iBanner .swiper-pagination-fraction {
  bottom: 40px;
}
.iBanner .swiper-pagination-bullet-active {
  background: #b3a480;
  width: 25px !important;
}
.iBanner .swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  border-radius: 99px;
  /* margin: 0 !important; */
  /* vertical-align: bottom; */
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}
/* 箭頭 */
.iBanner .swiper-button-prev, .iBanner .swiper-button-next {
  border-top: 1px solid #afafaf;
  border-right: 1px solid #afafaf;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
  margin-top: 20px;
  background-color: transparent !important;
  border-radius: 0 !important;
  width: 40px !important;
  height: 40px !important;
  box-shadow: none !important;
  position: absolute;
}
.iBanner .swiper-button-prev:after, .iBanner .swiper-button-next:after {
  content: none;
}
.iBanner .swiper-button-prev {
  left: 20px !important;
  top: 50%;
  margin: -10px 0 0;
  border-bottom: 1px solid #afafaf;
  border-left: 1px solid #afafaf;
  border-top: 0;
  border-right: 0;
}
.iBanner .swiper-button-next {
  right: 20px !important;
  top: 50%;
  margin: -10px 0 0
}

.iBanner .iBanPic {
  display: block;
  height: 100vh;
  background-position: center;
  background-size: cover;
}
.iBanner .swiper-slide {
  height: 100vh;
}

.foreground {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0 5%;
  top: 0;
  /* visibility: hidden; */
}
.foreground .box {
  -webkit-transition: all 2s ease-in-out;
  -moz-transition: all 2s ease-in-out;
  -ms-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
  display: block;
  position: absolute;
  top: 50%;
  z-index: 10;
  width: 100%;
  max-width: 500px;
  text-align: left;
  -moz-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}
.foreground .subject {
  display: block;
  font-size: 50px;
  letter-spacing: 10px;
  font-weight: 700;
  font-family: "Open Sans", "Myriad Pro", Myriad, Aria, sans-serif, 微軟正黑體;
  /* font-family: 'Playfair Display', serif; */
  /* font-family: 'Prata', serif; */
  text-transform: uppercase;
  /* background: linear-gradient(to top, #cbba89, #978a65); */
  background: linear-gradient(to top, #707070, #afafaf);
  background: -webkit-linear-gradient(to top, #707070, #afafaf);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.foreground .summary {
  display: block;
  color: #707070;
  text-shadow: 1px 1px 1px #efefef;
  padding-top: 20px;
  font-size: 15px;
}
.summary {
  font-size: 16px;
  letter-spacing: .8px;
  line-height: 25px;
  font-weight: 300;
}
.foreground .btn {
  display: inline-block;
  border: 1px solid #707070;
  padding: 10px 100px;
  color: #707070;
  text-align: center;
  font-size: 15px;
  border-radius: 10px;
  margin-top: 20px;
  /* background-color: rgba(0, 0, 0, 0.1); */
}

/* 按鈕 */
.btn-wrap,.iActionBR .btn-wrap {
    margin-top: 30px;
    text-align: left;
    -webkit-transition: all .5s ease 0s;
    -moz-transition: all .5s ease 0s;
    transition: all .5s ease 0s;
}
.iNewsMask:hover .txt,.iNewsMask:hover .btn-wrap,.iActionBR:hover .btn-wrap,.iActionBR a:hover .tiBox .txt {
    -webkit-transition-delay: .2s;
    transition-delay: .2s;
    -webkit-transition: all .5s ease 0s;
    -moz-transition: all .5s ease 0s;
    transition: all .5s ease 0s;
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
}
/* .iActionBR a .tiBox .btn-wrap {
    opacity: 0
} */
.iNewsMask:hover .btn-wrap,.iActionBR a:hover .tiBox .btn-wrap {
    -ms-filter: "progid: DXImageTransform.Microsoft.Alpha(Opacity=100)";
    filter: alpha(opacity=100);
    opacity: 1;
    -webkit-transition-delay: .2s;
    transition-delay: .2s;
}
.g-btn,.iActionBR .g-btn {
  position: relative;
  display: inline-block;
  width: 190px;
  height: 45px;
  font-size: 12px;
  line-height: 45px;
  text-align: center;
  white-space: nowrap;
  margin: 0;
  padding: 0 10px;
  border-style: solid;
  border-width: 1px;
  font-weight: 600;
  -moz-box-shadow: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  -moz-transition: background .22s cubic-bezier(.86,.13,.71,.94) .22s,color .2s cubic-bezier(.86,.13,.71,.94) .22s;
  -o-transition: background .22s cubic-bezier(.86,.13,.71,.94) .22s,color .2s cubic-bezier(.86,.13,.71,.94) .22s;
  -webkit-transition: background .22s cubic-bezier(.86,.13,.71,.94),color .2s cubic-bezier(.86,.13,.71,.94);
  -webkit-transition-delay: .22s,.22s;
  transition: background .22s cubic-bezier(.86,.13,.71,.94) .22s,color .2s cubic-bezier(.86,.13,.71,.94) .22s;
  border-color: transparent;
  /* background-color: transparent; */
  background-color: #b3a48020;
  color: #707070;
}
.g-btn:before, .g-btn:after {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  z-index: 0;
  pointer-events: none;
  top: -1px;
  -moz-transition: width .2s cubic-bezier(.86,.13,.71,.94) .02s,height .2s cubic-bezier(.86,.13,.71,.94) .02s;
  -o-transition: width .2s cubic-bezier(.86,.13,.71,.94) .02s,height .2s cubic-bezier(.86,.13,.71,.94) .02s;
  -webkit-transition: width .2s cubic-bezier(.86,.13,.71,.94),height .2s cubic-bezier(.86,.13,.71,.94);
  -webkit-transition-delay: .02s,.02s;
  transition: width .2s cubic-bezier(.86,.13,.71,.94) .02s,height .2s cubic-bezier(.86,.13,.71,.94) .02s;
}
.g-btn:before, .iActionBR .g-btn:before {
  border-top: 1px solid #b3a480;
  border-left: 1px solid #b3a480;
  left: -1px;
}
.g-btn:after, .iActionBR .g-btn:after {
  border-top: 1px solid #b3a480;
  border-right: 1px solid #b3a480;
  right: -1px;
}
.g-btn span, .iActionBR .g-btn span {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
}
.g-btn span:before, .g-btn span:after {
  content: "";
  width: 10px;
  height: 10px;
  position: absolute;
  z-index: 0;
  pointer-events: none;
  bottom: -1px;
  -moz-transition: width .2s cubic-bezier(.86,.13,.71,.94) .02s,height .2s cubic-bezier(.86,.13,.71,.94) .02s;
  -o-transition: width .2s cubic-bezier(.86,.13,.71,.94) .02s,height .2s cubic-bezier(.86,.13,.71,.94) .02s;
  -webkit-transition: width .2s cubic-bezier(.86,.13,.71,.94),height .2s cubic-bezier(.86,.13,.71,.94);
  -webkit-transition-delay: .02s,.02s;
  transition: width .2s cubic-bezier(.86,.13,.71,.94) .02s,height .2s cubic-bezier(.86,.13,.71,.94) .02s;
}
.g-btn span:before {
  border-bottom: 1px solid #b3a480;
  border-left: 1px solid #b3a480;
  left: -1px;
}
.g-btn span:after {
  border-bottom: 1px solid #b3a480;
  border-right: 1px solid #b3a480;
  right: -1px;
}
.g-btn:hover {
  color: #fff;
  background: #b3a480;
}
.g-btn:hover:before, .g-btn:hover:after,.iActionBR .g-btn:hover:before,.iActionBR .g-btn:hover:after {
  width: 100%;
  height: 100%;
}
.g-btn:hover span:before, .g-btn:hover span:after, .iActionBR .g-btn:hover span:before,.iActionBR .g-btn:hover span:after {
  width: 100%;
  height: 100%;
}
.g-btn:link,.iNewsMask .g-btn:visted, .g-btn:focus, .iActionBR .g-btn:link,.iActionBR .g-btn:visted,.iActionBR .g-btn:focus {
  color: #222
}

@media only screen and (max-width: 767px) {
  .iBanner .iBanPic {
    height: 400px;
  }
  .iBanner .swiper-slide {
    height: 100%;
  }

  .foreground {
    position: static;
    padding: 120px 5% 50px;
    background-color: #cecece;
    display: block;
  }
  .foreground .box {
    display: block;
    position: static;
    max-width: none;
    transform: translate(0, 0);
  }
  .foreground .subject {
    font-size: 40px;
    height: 60px;
    overflow: hidden;
  }
  .foreground .summary {
    height: 120px;
    overflow: hidden;
  }
  .iBanner .swiper-button-prev, .iBanner .swiper-button-next {
    display: none;
  }
}
</style>

<script>
/* swiper */
// 安裝：npm i swiper
import { Navigation, Pagination, Scrollbar, A11y, Autoplay, EffectFade } from 'swiper'; // 要 Autoplay、EffectFade，要載入
import { Swiper, SwiperSlide } from 'swiper/vue';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
import 'swiper/css/effect-fade';
export default {
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      carousel: [
        {
          img: `${require('@/assets/images/i_banner_1.jpg')}`,
          link: '/portfolioList',
          title: 'Web',
          summary: '在工作這段期間學到很多網站相關知識，從企劃或行銷那邊接到企劃書開始到完成整個網站前端視覺部分；從內頁排版到切版然後製作網頁效果為止。雖然很辛苦，但完成一個網站的時候那種成就感是無法形容的。',
        },
        {
          img: `${require('@/assets/images/i_banner_2.jpg')}`,
          link: 'https://www.flickr.com/photos/109072465@N04/',
          title: 'Illustration',
          summary: '雖然工作較多是接觸網頁設計方面，但我還是對於電腦繪圖和插畫非常感興趣，在家不時就會拿出手繪板畫個幾下。對於畫圖我又非常的龜毛，對於線條或輪廓總是要求要非常滑順，不喜歡有一點歪，不然心裡就會覺得很難受。因為這樣，我常常因為一個筆畫而重畫了好幾十次，重複著Ctrl+Z在畫一筆在Ctrl+Z。',
        },
        {
          img: `${require('@/assets/images/i_banner_3.jpg')}`,
          link: 'https://www.flickr.com/photos/109072465@N04/',
          title: 'Graphic',
          summary: '對於平面設計我覺得我還有很大的學習空間 (不只平面吧，每樣都還有很大學習空間吧) ，因為我不常做平面相關的東西，像海報、DM、書籍等等，但對於平面我常常會欣賞別人的作品，也會收集酷卡、DM文宣等東西，來促進平面更加精進。',
        },
      ],
    }
  },
  // inject: ['emitter'], // 內層元件用Inject的方式把emitter讀進來
  mounted() {
    
  },
  setup() {
    const onSwiper = (swiper) => {
      // console.log(swiper);
    };
    const onSlideChange = () => {
      // console.log('slide change');
    };
    return {
      onSwiper,
      onSlideChange,
      modules: [Navigation, Pagination, Scrollbar, A11y, Autoplay, EffectFade], // 要 Autoplay、EffectFade，要載入
    };
  },
}
</script>