<template>
  <header class="gHideHeader" ref="gHideHeader">
    <div class="gTopMenu">
      <a href="#cd-navigation" class="nav-trigger" @click.prevent="openNav">
        <span>
          <em aria-hidden="true"></em>
        </span>
      </a>
      <h1 title="雯守"><router-link to="/" title="雯守"><img src="@/assets/images/logo.png" alt="雯守"></router-link></h1>
      <nav class="cd-primary-nav">
        <ul id="cd-navigation">
          <!-- <li v-for="item in menu" :key="item"><router-link :to="`/products/${item}`">{{item}}</router-link></li> -->
          <li><router-link to="/portfolioList">網頁相關</router-link></li>
          <li><a href="https://www.flickr.com/photos/109072465@N04/" target="_blank">平面相關</a></li>
          <li><a href="https://janbond.github.io/old_portfolio/" target="_blank">2015年前作品</a></li>
        </ul>
      </nav>
    </div>
  </header>
</template>

<script>
export default {
  data() {
    return {
      currTop: 0,
    }
  },
  methods: {
    openNav() {
      const mainHeader = this.$refs.gHideHeader;
      mainHeader.classList.toggle('nav-open');
    },
    scrollHeader() {
      this.currTop = window.scrollY;
    },
  },
  watch: {
    currTop(newVal, oldVal) {
      // console.log('newVal', newVal, 'oldVal', oldVal)
      const mainHeader = this.$refs.gHideHeader;
      if(newVal < 200) {
        mainHeader.classList.remove('is-scroll');
      } else {
        mainHeader.classList.add('is-scroll');
      }
    }
  },
  created() {
    window.addEventListener('scroll', this.scrollHeader)
  },
}
</script>