import { createStore } from "vuex";

export default createStore({
  state: {
    works: [],
    work: {},
  },
  getters: {},
  mutations: {
    setWorks(state, payload) {
      state.works = payload;
    },
    setWork(state, payload) {
      state.work = payload;
    }
  },
  actions: {
    getWorks(context, id) {
      fetch('./works.json')
      .then(rs => rs.json())
      .then(res => {
        console.log('取得作品', res)
        if(!id) {
          context.commit('setWorks', res)
        } else {
          const thisWork = res.find(item => item.id == id)
          context.commit('setWork', thisWork)
        }
        
      })
    },
    
  },
  modules: {},
});
